'use client';

import { useState } from 'react';
import Image from 'next/image';

export default function HTMLBasics() {
  const [code, setCode] = useState('');
  const [isCorrect, setIsCorrect] = useState(false);

  const checkAnswer = () => {
    const correctAnswer = '<h1>你好，小丸子！</h1>';
    setIsCorrect(code.trim() === correctAnswer);
  };

  return (
    <div className="max-w-4xl mx-auto py-8 px-4">
      <div className="text-center mb-8">
        <div className="relative w-24 h-24 mx-auto mb-4">
          <Image
            src="/practice/images/樱桃小丸子图片生成 (6).png"
            alt="Maruko"
            fill
            className="rounded-full object-cover"
          />
        </div>
        <h1 className="text-2xl font-bold text-[#FF6B6B] mb-2">HTML标题标签练习</h1>
        <p className="text-gray-600">让我们学习如何创建一个漂亮的标题！</p>
      </div>

      <div className="maruko-card p-6 mb-8">
        <h2 className="text-lg font-bold text-[#FF6B6B] mb-4">任务说明</h2>
        <p className="text-gray-600 mb-4">
          使用HTML的h1标签创建一个标题，内容为"你好，小丸子！"
        </p>
        <div className="bg-gray-100 p-4 rounded-lg">
          <code className="text-gray-700">示例: &lt;h1&gt;标题文字&lt;/h1&gt;</code>
        </div>
      </div>

      <div className="maruko-card p-6 mb-8">
        <h2 className="text-lg font-bold text-[#FF6B6B] mb-4">编写代码</h2>
        <textarea
          value={code}
          onChange={(e) => setCode(e.target.value)}
          className="w-full h-32 p-4 border rounded-lg font-mono text-sm"
          placeholder="在这里输入你的代码..."
        />
        <button
          onClick={checkAnswer}
          className="maruko-button mt-4"
        >
          检查答案
        </button>
      </div>

      {isCorrect && (
        <div className="maruko-card p-6 bg-green-50">
          <div className="flex items-center">
            <Image
              src="/practice/images/樱桃小丸子图片生成 (7).png"
              alt="Success"
              width={60}
              height={60}
              className="rounded-full mr-4"
            />
            <div>
              <h3 className="text-lg font-bold text-green-600 mb-2">太棒了！</h3>
              <p className="text-green-600">
                你成功创建了一个HTML标题！让我们继续下一个练习吧！
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
} 